@use '../../../base/01-base' as base;

.eui-19 .eui-icon-svg.eui-input-text--clearable-icon {
    cursor: pointer;
    position: absolute;
    right: var(--eui-s-s);
    user-select: none;
    visibility: visible;
    z-index: var(--eui-zi-clearable-icon);
    @include base.eui-accessible-focus();

    > svg {
        fill: var(--eui-c-text-light);

        &:hover {
            fill: var(--eui-c-s-danger);
        }
    }
}
