.eui-19 {
    table.eui-table-default {
        background-color: transparent;
        border-collapse: collapse;
        border-spacing: 0;
        color: var(--eui-c-text);
        max-width: 100%;
        overflow: auto;
        position: relative;
    
        // Add default borders
        th {
            border: 1px solid var(--eui-c-divider);
        }

        th,
        td {
            &:first-child {
                border-left: 0;
            }
    
            &:last-child {
                border-right: 0;
            }
        }
    
        // Overall settings (default)
        thead > tr > th,
        tbody > tr > td,
        tfoot > tr > td {
            vertical-align: top;
        }
    
        tr > th,
        tr > td {
            padding: var(--eui-s-s);
            text-align: left;
        }
    
        thead {
            th {
                color: var(--eui-c-text);
                font-size: var(--eui-f-size-base);
                padding: var(--eui-s-m) var(--eui-s-s);
                vertical-align: middle;
                font: var(--eui-f-m-bold);
            }
    
            tr {
                background: var(--eui-c-surface-container-2);
                border-top: 0;
            }
        }
    
        // Striped & hovered tables by default (UX)
        tbody {
            -webkit-overflow-scrolling: touch;  // optional - enable iOS momentum scrolling
    
            tr:nth-of-type(odd) {
                background-color: var(--eui-c-surface-page);
    
                &:hover {
                    background-color: var(--eui-c-active-bg);
                }
            }
    
            tr:nth-of-type(even) {
                background-color: var(--eui-c-surface-container-1);
    
                &:hover {
                    background-color: var(--eui-c-active-bg);
                }
            }
        }
    
        tfoot {
            tr {
                background: var(--eui-c-surface-container-2);
            }
        }
    
        tr {
            border-bottom: 1px solid var(--eui-c-divider-light);
            display: table-row;
        }
    
        // Compact table
        &--compact {
            thead {
                th {
                    padding: var(--eui-s-xs) var(--eui-s-s);
                }
            }
    
            tr > td {
                padding: var(--eui-s-2xs) var(--eui-s-s);
            }
        }
    
        // Responsive table
        &--responsive {
            display: table;
            width: 100%;
    
            &--bordered {
                border: 0;  // Prevent double border on horizontal scroll due to use of `display: block;`
            }
        }
    
        // NoWrap table content (also disables ellipsis)
        &--nowrap {
            white-space: nowrap;
    
            thead > tr > th,
            tbody > tr > td,
            tfoot > tr > td {
                white-space: nowrap;
            }
        }
    
        // Centered table
        &--centered {
            display: table;
            margin: 0 auto;
            width: auto;
        }
    
        // Middle aligned cells content
        &--align-middle {
            thead > tr > th,
            tbody > tr > td,
            tfoot > tr > td {
                vertical-align: middle;
            }
        }
    }
}
