@use '../../../base/01-base' as base;

// TODO v21: to be removed. See if those can be part of eui-button.states / variants
// BUTTON FIXED (used in Showcase shared components)
.eui-button-fixed {
    bottom: var(--eui-s-3xl);
    position: fixed;
    right: var(--eui-s-xl);
    z-index: calc(#{var(--eui-zi-btn-floating)} + 1);  // Allow to be over the footer & page columns
    @include base.eui-accessible-focus();
}

// BUTTON FLOATING
.eui-button-floating {
    align-items: center;
    border-radius: var(--eui-br-max);
    display: inline-flex;
    overflow: hidden;
    padding: var(--eui-s-xs);
    position: relative;
    transform: translateY(35%);
    vertical-align: middle;
    height: var(--eui-s-4xl);
    width: var(--eui-s-4xl);
    z-index: 1;
    box-shadow: var(--eui-sh-6);
    background-color: var(--_bg-color);
    color: var(--_on-bg-color);
    border: none;

    &:hover {
        cursor: pointer;
        box-shadow: var(--eui-sh-3);
    }

    &::before {
        border-radius: var(--eui-br-none);
    }
}

// TODO v21: to be removed an use the eui-icon-button-expander component instead
// BUTTON AS EXPAND TRIGGER
// Seel also --expand-toggle in button.states
.eui-button__expand-trigger {
    border-top: 1px solid var(--eui-c-divider);
    cursor: pointer;
    display: flex;
    justify-content: center;
    width: 100%;
    z-index: 1;
    margin-bottom: var(--eui-s-l);

    &:hover {
        .eui-button--expand-toggle {
            box-shadow: var(--eui-sh-3);
        }
    }

    &--collapsed {
        margin-top: var(--eui-s-s);
    }
}
.eui-button--expand-toggle {
    background: var(--eui-c-surface-page);
    border-color: transparent;
    border-radius: 0 0 var(--eui-s-xs) var(--eui-s-xs);
    cursor: pointer;
    min-height: var(--eui-s-m);
    padding: 0 var(--eui-s-s);
    box-shadow: var(--eui-sh-1);
    display: inline-flex;
    align-items: center;
    gap: var(--eui-s-xs);

    &:hover,
    &:focus {
        box-shadow: var(--eui-sh-3);
    }
}

// TODO v21: to be removed. Create a component wrapper for this composition (ex: see eui-chip-group)
// DROPDOWN BUTTON (Composition)
.eui-dropdown-button {
    align-items: center;
    display: inline-flex;
    flex-shrink: 0;
    justify-content: center;
    position: relative;

    &--disabled,
    &:disabled {
        pointer-events: none;
    }
}
