.eui-19 {

    // MATERIAL Drag & Drop overrides
    .cdk-drag-handle {
        cursor: move !important;
    }
    
    .cdk-drop-list {
        display: flex;
        min-width: 100%;
    
        &:not(.cdk-drop-list-disabled) {
            min-height: var(--eui-s-xl);
        }
    }
    
    // Animate items as they're being sorted
    .cdk-drop-list-dragging .cdk-drag {
        transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
    }
    
    // Animate an item that has been dropped
    .cdk-drag-animating {
        transition: transform 300ms cubic-bezier(0, 0, 0.2, 1);
    }
    
    // Grabbed element
    .cdk-drag-preview {
        background-color: transparent;  // var(--eui-c-white);
        box-shadow: none;
        cursor: grab !important;
        display: table;     // trick to get the full row width
        margin: 0 !important;
        opacity: 1;
        position: relative;
    }
    
    .cdk-drag-dragging {
        opacity: 1;
    }
    
    .cdk-drag-placeholder {
        background-color: transparent;
        opacity: 0.35;
    
        &:hover {
            outline: 2px dashed var(--eui-c-s-secondary-border-light);
            outline-offset: 2px;
        }
    }
}

