@use '../../../base/01-base' as base;
@use '../../../base/02-tokens/maps' as tokens;

.eui-19 {
    // Disable global scrollbars (used in full screen eui-dialog for example)
    .cdk-global-scrollblock {
        position: fixed;
        overflow-y: visible;     // Material default: scroll
        width: 100%;
        @include base.eui-scrollbars;
        left: 0 !important;
    }
    @include base.media(tokens.$eui-bkp-mobile) {
        .cdk-global-scrollblock {
            height: 100%;
            overflow: auto;
            position: fixed;
            width: 100%;
            @include base.eui-scrollbars;
        }
    }
}


