#loader-wrapper {
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;

    #loader {
        -webkit-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
        animation: spin 2s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */
        animation: hourglass 5s linear infinite;
        border: 10px solid transparent;
        border-radius: 50%;
        border-top-color: var(--eui-c-s-warning-base);
        display: block;
        height: 200px;
        left: 50%;
        margin: -100px 0 0 -100px;
        position: relative;
        top: 50%;
        width: 200px;

        &::before {
            -webkit-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
            animation: spin 2s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */
            animation: hourglass 4s linear infinite;
            border: 10px solid transparent;
            border-radius: 50%;
            border-top-color: var(--eui-c-s-danger-base);
            bottom: 5px;
            content: "";
            left: 5px;
            position: absolute;
            right: 5px;
            top: 5px;
        }

        &::after {
            -webkit-animation: spin 1s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
            animation: spin 1s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */
            animation: hourglass 3s linear infinite;
            border: 10px solid transparent;
            border-radius: 50%;
            border-top-color: var(--eui-c-s-primary-base);
            bottom: 20px;
            content: "";
            left: 20px;
            position: absolute;
            right: 20px;
            top: 20px;
        }
    }
}

/* stylelint-enable */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@-webkit-keyframes hourglass  {
    0% {
        transform: rotate(0);
        animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
        transform: rotate(900deg);
        animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
        transform: rotate(1800deg);
    }
}
@keyframes hourglass {
    0% {
        transform: rotate(0);
        animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
        transform: rotate(900deg);
        animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
        transform: rotate(1800deg);
    }
}
